













import Vue from "vue";
import { Organization } from "../core/model/organization";
import { formatDate } from "../core/date-formatter";

export default Vue.extend({
  name: "Organization",
  props: {
    org: {
      type: Organization,
      required: true,
    },
  },
  filters: {
    formatDate(value: Date) {
      return formatDate(value);
    },
  },
});
