











import Vue from "vue";
import { User } from "../core/model/user";
import { formatDate } from "../core/date-formatter";

export default Vue.extend({
  name: "User",
  props: {
    user: {
      type: User,
      required: true,
    },
  },
  filters: {
    formatDate(value: Date) {
      return formatDate(value);
    },
  },
});
