














import Vue from "vue";

export default Vue.extend({
  name: "Pagination",
  props: {
    isNext: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      page: 1,
    };
  },
  computed: {
    isPrev(): boolean {
      return this.page > 1;
    },
  },
  methods: {
    prev(): void {
      if (this.page > 0) {
        this.page--;
        this.$emit("prev-page");
      }
    },
    next(): void {
      if (this.isNext) {
        this.page++;
        this.$emit("next-page");
      }
    },
  },
});
